/* =================================== news-block @mixin ================================== */
.news-block__actions { display: flex; align-items: center; margin-bottom: 50px; }

.news-block__actions-btn { margin-right: 28px; }

.news-block__actions-link { font-family: "ProximaNova-Bold", Arial, sans-serif; font-weight: normal; font-size: 15px; line-height: 18px; color: #0f2740; text-transform: uppercase; letter-spacing: 0.05em; border-bottom: 1px solid rgba(15, 39, 64, 0.3); transition: border-color 0.3s; }

.no-touch .news-block__actions-link:hover { border-bottom-color: transparent; }

.news-block__slider { padding-bottom: 80px; display: none; }

.news-block__slider.slick-initialized { display: block; }

.news-block__slider .slick-list { margin: 0 -15px; }

.news-block__slider-item { margin: 0 15px; }

.news-block__slider .slick-dots { display: flex; position: absolute; left: 50%; bottom: 7px; z-index: 2; transform: translateX(-50%); }

.news-block__slider .slick-dots li { position: relative; width: 14px; height: 14px; border: 3px solid transparent; border-radius: 50%; cursor: pointer; transition: border-color .7s ease-in-out; }

.news-block__slider .slick-dots li:not(:first-child) { margin-left: 3px; }

.news-block__slider .slick-dots li.slick-active { border-color: #26b2b5; }

.news-block__slider .slick-dots li.slick-active button { background: transparent; }

.news-block__slider .slick-dots button { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font: 0/0 a; text-shadow: none; color: transparent; width: 10px; height: 10px; background: #e0f4f5; border-radius: 50%; }

.news-block_default { padding: 103px 0; }

/* =================================== news-article @mixin ================================== */
.news-article { outline: 0; }

.news-article__title { margin-bottom: 15px; font-size: 22px; line-height: 25px; }

.news-article__descr { margin: 14px 0 19px; }

.news-article__img-wrap { height: 210px; }

.news-article__actions { display: flex; align-items: center; }

.news-article__actions-btn { margin-right: 9px; padding: 6px 25px; }

.news-article__actions-link { font-size: 0; line-height: 0; }

.no-touch .news-article__actions-link:hover .icon-share { fill: #00899a; }

.news-article__actions-link .icon-share { fill: #0f2740; font-size: 20px; transition: fill .2s ease-in-out; }

.subscribe { padding: 48px 0 45px; background: url("../img/bg/subscribe_bg.png") repeat-x 50% 50%/cover; }

.subscribe__container { display: flex; align-items: center; }

.subscribe__descr { flex: 0 0 265px; margin: 0 25px; color: #fff; line-height: 20px; }

.subscribe__form { flex: 1 1 auto; }

.subscribe__form-in { display: flex; }

.subscribe__form-field { flex: 1 1 auto; margin-right: 10px; }

.subscribe__form-btn { padding: 11px 39px; }

.our__container { display: flex; justify-content: space-between; flex-wrap: wrap; max-width: 1190px; }

.our__block { flex: 0 1 560px; min-width: 1px; }

.our__block:nth-child(2) .our__title { margin: 0 0 25px 27px; }

.our__block:nth-child(2) .our__slider-btn_prev { right: 220px; }

.our__block:nth-child(2) .our__slider-btn_next { right: 179px; }

.our__title { max-width: 270px; margin: 0 0 25px 22px; }

.our__slider { display: none; background: url("../img/bg/our-block_bg.png") no-repeat 50% 50%; }

.our__slider.slick-initialized { display: block; }

.our__slider-btn { position: absolute; top: -65px; z-index: 1; width: 40px; height: 40px; background: #2ab1b4; transition: background .3s ease; text-align: center; font-size: 0; }

.our__slider-btn:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.no-touch .our__slider-btn:hover { background: #00899a; }

.our__slider-btn_prev { right: 206px; }

.our__slider-btn_next { right: 165px; }

.our__slider-btn-icon { display: inline-block; vertical-align: middle; fill: #fff; font-size: 12px; }

.our__item-inner { display: flex; flex-direction: column; justify-content: center; align-items: center; height: 80px; }

.our__item-client { position: relative; }

.no-touch .our__item-client:hover .our__item-img { opacity: 0; }

.no-touch .our__item-client:hover .our__item-img_hidden { opacity: 1; }

.our__item-img { transition: opacity .3s ease-in-out; }

.our__item-img_hidden { position: absolute; top: 0; left: 0; width: 100%; height: 100%; opacity: 0; background-size: contain; background: no-repeat 50% 50%; }

.our__btn { margin: 45px auto 0; padding: 10px 43px; }

.our_default { padding: 93px 0 90px; }

.contact-block { display: flex; align-items: center; padding: 80px 0 90px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.contact-block__container { display: flex; flex-direction: column; align-items: center; flex: 1 1 100%; max-width: 550px; }

.contact-block__phone { margin-bottom: 10px; }

.contact-block__email { font-size: 35px; line-height: 35px; color: #fff; border-bottom: 1px solid #cfebee; transition: border-color 0.3s; }

.no-touch .contact-block__email:hover { border-bottom-color: transparent; }

.contact-block__address { margin: 15px 0 33px; font-style: normal; font-size: 20px; line-height: 25px; color: #fff; text-align: center; }

.contact-block__actions { margin: 0 -20px; }

.contact-block__actions-btn { margin: 0 10px; padding: 11px 39px; }

.socials { display: flex; }

.socials__link { display: flex; align-items: center; justify-content: center; height: 35px; width: 35px; border-radius: 50%; background: #2ab1b4; font-size: 0; }

.socials__link:not(:first-child) { margin-left: 6px; }

.no-touch .socials__link:hover .socials__link-icon { fill: #0f2740; }

.socials__link-icon { fill: #fff; transition: fill .2s ease; }

.socials__link-icon.icon-vk { font-size: 8px; }

.socials__link-icon.icon-fb { font-size: 16px; }

.socials__link-icon.icon-yt { margin-bottom: 1px; font-size: 11px; }

.socials__link-icon.icon-tw { font-size: 12px; }

